import { DownloadOutlined, UploadOutlined } from "@ant-design/icons";
import { Table, Pagination, Row, Col } from "antd";
import {
  CFixAssets,
  CError,
  CListData,
  CLoadingSpinner,
  CTitle,
  ErrorNotification,
} from "@components";
import {
  FChange,
  FDownloadTemplate,
  FGetAccess,
  FGetDetailData,
  FGetMoreData,
  FGetTableData,
  FImportData,
  FReadFile,
  FRemoveMaster,
  FSubmitMaster,
} from "@function";
import { useRouter } from "next/router";
import React, { useEffect, useState } from "react";

const FixAssets = () => {
  const router = useRouter();
  const [total, setTotal] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const [loading, setLoading] = useState(false);
  const [isEmpty, setIsEmpty] = useState(true);
  const [isForms, setIsForms] = useState(false);
  const [showTable, setShowTable] = useState(false);
  const [data, setData] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [errors, setErrors] = useState({});
  const [accessList, setAccessList] = useState({
    viewAny: true,
    view: true,
    create: true,
    update: true,
    delete: true,
  });
  const [paginate, setPaginate] = useState({
    pageSize: 15,
    current_page: 1,
    keyword: "",
  });
  const [forms, setForms] = useState({
    id: "",
    code: "",
    name: "",
    description: "",
    provinsi: "",
    provinsi_id: "",
    kota_id: "",
  });

  function handleSearch(e) {
    setPaginate({
      ...paginate,
      [e.target.id]: e.target.value,
    });
  }
  const onRefresh = () => {
    setPaginate({
      pageSize: 15,
      current_page: 1,
      keyword: "",
    });
    setCurrentPage(1);
    getData();
  };

  useEffect(() => {
    FGetAccess(router, setAccessList, setIsLoading);
  }, []);

  useEffect(() => {
    getData();
  }, [paginate]);

  const getData = () => {
    FGetTableData(
      setLoading,
      "api/asset/fix-asset",
      paginate,
      setData,
      setTotal
    );
  };

  const getMoreData = () => {
    FGetMoreData(
      setLoading,
      "api/asset/fix-asset",
      paginate,
      setData,
      setTotal,
      data,
      currentPage,
      setCurrentPage
    );
  };

  //* Crud
  const handleEdit = (e, item) => {
    e.preventDefault();
    setIsEmpty(false);
    setShowTable(false);
    FGetDetailData("api/asset/fix-asset/" + item, setForms, setIsForms);
  };
  const handleSubmit = () => {
    setShowTable(false);
    FSubmitMaster(
      "api/asset/fix-asset",
      forms,
      setErrors,
      setIsForms,
      setIsEmpty,
      setForms,
      getData
    );
  };
  const handleBack = () => {
    setIsForms(false);
    setIsEmpty(true);
    setShowTable(false);
    setForms({
      id: "",
      code: "",
      name: "",
      description: "",
      provinsi: "",
      provinsi_id: "",
      kota_id: "",
    });
  };
  const handleInsert = () => {
    setIsForms(false);
    setShowTable(false);
    setForms({
      id: "",
      code: "",
      name: "",
      description: "",
      provinsi: "",
      provinsi_id: "",
      kota_id: "",
    });
    setTimeout(() => {
      setIsForms(true);
    }, 50);
    setIsEmpty(false);
  };
  const handleRemove = () => {
    if (forms.id > 0) {
      FRemoveMaster(
        forms.id,
        "api/asset/fix-asset",
        getData,
        setIsForms,
        setIsEmpty,
        setForms
      );
    } else {
      ErrorNotification("Error", "Please select data...");
    }
  };
  const handleChange = (e) => {
    FChange(e, forms, setForms);
  };
  const handleImport = () => {
    const f = document.getElementById("input-file");
    f.click();
  };
  const handleDownload = async () => {
    FDownloadTemplate(
      "api/master/template/branch",
      "Template Master Branch.xlsx"
    );
  };
  const onReadFile = (file) => {
    FReadFile(file.target.files[0], dataToImport);
  };
  const dataToImport = (res) => {
    var data = res.map((r) => {
      return { code: r[1] || "", name: r[2] || "", description: r[3] || "" };
    });
    onImportData(data);
  };
  const onImportData = (data) => {
    FImportData(
      "api/master/import/branch",
      data,
      getData,
      setLoading,
      "input-file"
    );
  };

  const items = [
    {
      key: "2",
      label: (
        <div onClick={handleImport}>
          <UploadOutlined /> Import Master
        </div>
      ),
    },
    {
      key: "3",
      label: (
        <div onClick={handleDownload}>
          <DownloadOutlined /> Download Template
        </div>
      ),
    },
  ];

  //* End Crud

  if (isLoading) return <CLoadingSpinner />;
  if (!accessList.viewAny) return <CError status={403} />;

  const columns = [
    { key: "id", title: "ID", dataIndex: "id", fixed: "left", width: 40 },
    {
      key: "fix_asset_group",
      title: "Fix Asset Group",
      dataIndex: "fix_asset_group",
      width: 200,
    },
    { key: "number", title: "Number", dataIndex: "number", width: 200 },
    {
      key: "search_name",
      title: "Search Name",
      dataIndex: "search_name",
      width: 200,
    },
    { key: "type", title: "Type", dataIndex: "type", width: 100 },
    {
      key: "major_type",
      title: "Major Type",
      dataIndex: "major_type",
      width: 200,
    },
    {
      key: "property_type",
      title: "Property Type",
      dataIndex: "property_type",
      width: 200,
    },
    { key: "quantity", title: "Quantity", dataIndex: "quantity", width: 100 },
    {
      key: "unit_of_measure",
      title: "Unit Of Measure",
      dataIndex: "unit_of_measure",
      width: 200,
    },
    {
      key: "unit_cost",
      title: "Unit Cost",
      dataIndex: "unit_cost",
      width: 100,
    },
  ];

  return (
    <>
      <CTitle />
      <CListData
        onDetail={handleEdit}
        handleSearch={handleSearch}
        refresh={onRefresh}
        data={data}
        total={total}
        paginate={paginate}
        loading={loading}
        accessPermission={accessList}
        onLoadMore={getMoreData}
        handleBack={handleBack}
        handleRemove={handleRemove}
        items={items}
        handleInsert={handleInsert}
        isForms={isForms}
        handleSubmit={handleSubmit}
        id={forms.id}
        showTable={showTable}
        showTableTogle
        onTableTogle={() => setShowTable(showTable ? false : true)}
        children={
          showTable ? (
            <Row>
              <Col span={24}>
                <Table
                  dataSource={data}
                  columns={columns}
                  size="small"
                  rowKey="id"
                  loading={loading}
                  pagination={false}
                  bordered
                  scroll={{ x: 1800, y: 300 }}
                />
              </Col>
              <Col span={24} style={{ paddingTop: "10px" }}>
                <Pagination
                  style={{ float: "right" }}
                  showSizeChanger
                  // onShowSizeChange={onShowSizeChange}
                  defaultCurrent={1}
                  total={total}
                  current={paginate.current_page}
                  defaultPageSize={15}
                  pageSizeOptions={["15", "30", "50", "100", "200"]}
                  // onChange={onShowSizeChange}
                  size="small"
                />
              </Col>
            </Row>
          ) : (
            <CFixAssets
              isEmpty={isEmpty}
              forms={forms}
              isForms={isForms}
              handleChange={handleChange}
              errors={errors}
            />
          )
        }
      />
      <input
        type="file"
        style={{ display: "none" }}
        id="input-file"
        onChange={(file) => onReadFile(file)}
        name="file"
        accept=".xlsx, .xls"
      />
    </>
  );
};

export default FixAssets;
